import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';

// material-ui
import { Button, Divider, Typography, List, ListItem } from '@mui/material';
import { Check as CheckIcon, Cancel as CancelIcon } from '@mui/icons-material';

function Confirmation({ langPack, productModified, modificationSubmission }) {
    let navigate = useNavigate(); //react-router-dom
    let modifiedKeys = Object.keys(productModified);
    return (
        <>
            {modifiedKeys.length ? (
                <>
                    <Typography variant="h3">{langPack.steps.modificationDone}</Typography>
                    <List>
                        {modifiedKeys.map((key) => (
                            <ListItem key={key}>--{key}</ListItem>
                        ))}
                    </List>
                </>
            ) : (
                <Typography variant="h3">{langPack.steps.modificationNull}</Typography>
            )}
            <br />
            <Divider />
            <div align="right">
                <Button startIcon={<CheckIcon />} onClick={modificationSubmission}>
                    {langPack.common.yes}
                </Button>
                <Button startIcon={<CancelIcon />} onClick={() => navigate(`/product`)}>
                    {langPack.common.no}
                </Button>
            </div>
        </>
    );
}

Confirmation.propTypes = {
    langPack: PropTypes.object,
    productModified: PropTypes.object,
    modificationSubmission: PropTypes.func
};

export default Confirmation;
